import dateUtils from '@/helpers/dateUtils'
import { apiToLogo } from './companyConverters'

function relatedPartyToApi(company, data) {
  return {
    companyId: company.id,
    refCompanyId: data.contact,
    fromDate: data.fromDate,
    toDate: data.toDate,
    description: data.description,
  }
}

function apiToRelatedParty(data) {
  let relatedParties = []
  data.relatedParties.forEach((current) => {
    let companyAliases = ''
    current.companyAliases.forEach((alias) => {
      companyAliases += alias.aliasText + ', '
    })
    companyAliases = companyAliases.slice(0, -2)
    relatedParties.push({
      detailId: current.detailId,
      typeId: current.typeId,
      typeName: current.typeName,
      fromDate: new Date(current.fromDate),
      toDate: current.toDate !== null ? new Date(current.toDate) : null,
      name: current.name,
      country: current.country,
      description: current.description,
      registrationNumber: current.registrationNumber,
      companyAliases: companyAliases,
      logo: apiToLogo(current.name, current.logoUrl),
    })
  })
  return {
    relatedParties: relatedParties,
    totalCount: data.totalCount,
  }
}

function relatedPartiesPayloadToQuery(payload) {
  // console.log(payload)
  let queryString = payload.startDate !== null ? 'startDate=' + dateUtils.formatDate(payload.startDate) + '&' : ''
  queryString += payload.endDate !== null ? 'endDate=' + dateUtils.formatDate(payload.endDate) + '&' : ''
  queryString += payload.startIndex !== null ? 'startIndex=' + payload.startIndex + '&' : ''
  queryString += payload.endIndex !== null ? 'endIndex=' + payload.endIndex + '&' : ''
  queryString += payload.sort !== null ? 'sort=' + payload.sort + '&' : ''
  queryString += payload.isSortDescending !== null ? 'isSortDescending=' + payload.isSortDescending + '&' : ''
  if (queryString !== '') {
    queryString = queryString.slice(0, -1)
    return '?' + queryString
  } else {
    return ''
  }
}

function relatedPartiesPatchDataToApi(payload) {
  return {
    fromDate: payload.fromDate,
    toDate: payload.toDate ? payload.toDate : null,
    description: payload.description,
  }
}

export {
  relatedPartyToApi,
  apiToRelatedParty,
  relatedPartiesPayloadToQuery,
  relatedPartiesPatchDataToApi,
}



