import reconciliationService from '@/services/reconciliationService'

const INITIAL_STATE = {
  exceptionsRecords: [],
  matchedSets: [],
  SRMessages: [],
}

var state = {...INITIAL_STATE}

var getters = {}

// Generate getters for constants
Object.keys(state).reduce((obj, key) => {
  obj[key] = (state) => state[key]
  return obj
}, getters)

const mutations = {
  setMatchedSets(state, payload) {
    state.matchedSets = payload
  },
  setExceptionsRecords(state, payload) {
    state.exceptionsRecords = payload
  },
  setSRMessages(state, payload) {
    state.SRMessages = payload
  },
}

function thenHandler(commit, commitName) {
  return function(res) {
    commit(commitName, res)
    return res
  }
}

const actions = {
  resetReconciliation({state}) {
    Object.assign(state, INITIAL_STATE)
  },

  loadMatchedSets({ commit, state }, payload) {
    return reconciliationService.getMatchedSets(payload, state.selectedCompany)
      .then(thenHandler(commit, 'setMatchedSets'))
  },

  loadSRMessages({ commit, state }, payload) {
    return reconciliationService.getSRMessages(payload)
      .then(thenHandler(commit, 'setSRMessages'))
  },

  sendReconciliationMessage({ commit, state }, payload) {
    return reconciliationService.sendSRMessage(payload)
      .then()
  },

  deleteMessage({ commit, state }, payload) {
    return reconciliationService.deleteMessage(payload)
      .then()
  },

  editMessage({ commit, state }, payload) {
    return reconciliationService.editMessage(payload)
      .then()
  },

  loadExceptionsRecords({ commit, state }, payload) {
    return reconciliationService.getExceptionsRecords(payload, state.selectedCompany)
      .then(thenHandler(commit, 'setExceptionsRecords'))
  },

  matchAuto({ commit, state }, payload) {
    return reconciliationService.matchAuto(payload, state.selectedCompany)
      .then()
  },

  manualMatch({ commit, state }, payload) {
    return reconciliationService.manualMatch(payload, state.selectedCompany)
      .then()
  },

  unmatchSet({ commit, state }, payload) {
    return reconciliationService.unmatchSet(payload)
      .then()
  },

}

export default {
  state,
  getters,
  mutations,
  actions,
}



